// 
const SET_UNIQUEID = "SET_UNIQUEID";

//  
export const setUniqueId = (user) => ({
   type: SET_UNIQUEID,
   payload: user,
});

const initialState = {
   id: "",
   first: false,
};

export default function uniqueId(state = initialState, action) {
   switch (action.type) {
      case SET_UNIQUEID:
         return { ...action.payload };
      default:
         return state;
   }
}
